import React from "react";
import Header from "../Header";
import { useHistory, Link } from "react-router-dom";

import { useSelector } from "react-redux";

import axios from "axios";
import Footer from "../footer";

const EDITSpROFILE = () => {
  const history = useHistory();
  const [homepageData, setHomePageData] = React.useState({
    faq: [],
    Banners: [],
  });
  const [formReponse,setFomreponse] =React.useState({})
  const fetchHomepageModels = async () => {
    return await axios
      .get(`/api/v1/fetchHomepageModels`)
      .then((response) => response)
      .catch((err) => err);
  };
  const currentUser = useSelector(({ user }) => user.currentUser);
  const mapFaq = () => {
    return (
      homepageData.faq.length > 0 &&
      homepageData.faq.map((xxx, index) => (
        <div className="card border-0">
          <div
            className="st-top d-inline-block position-relative card-header px-0 border-0 rounded-0 bg-transparent"
            id="headingone"
          >
            <h5 className="mb-0">
              <button
                className="d-block border-0 bg-transparent p-0 text-left"
                data-toggle="collapse"
                data-target="#collapseone"
                aria-expanded="true"
                aria-controls="collapseone"
              >
                {xxx.title}
              </button>
            </h5>
          </div>
          <div
            id="collapseone"
            className="collapse show"
            aria-labelledby="headingone"
            data-parent="#accordion"
          >
            <div className="card-body mb-15">{xxx.body}</div>
          </div>
        </div>
      ))
    );
  };
  return (
    <div>
      <Header />
      <div>
        <div className="topPatch" style={{ height: "80px" }} />
        <div className="poststep pricing my-80">
          <div className="container">
            {/* <h3 className="color-secondary line-bottom pb-15 mb-20">
              Privacy Policy
            </h3> */}

            <span className="small-title color-primary position-relative line-primary">
              Edit My Profile
            </span>

            <div className="col-lg-12 border-top-1-gray py-30 wow slideInRight animated">
              <h3 className="color-secondary line-bottom pb-15 mb-20">
                My Details
              </h3>
              <div className="row  justify-content-around align-items-center">
                <div className="  avataer-edit"></div>
                <div className="col-md-8 col-lg-8">
                  <ul className="">
                    <li>
                      First Name:{" "}
                      <span>
                        <input value={currentUser.user.firstName} className="form-control bg-gray" type="text" />
                      </span>
                    </li>
                    <li>
                      Last Name :{" "}
                      <span>
                        <input value={currentUser.user.lastName} className="form-control bg-gray" type="text" />
                      </span>
                    </li>
                    <li>
                      Email:
                      <span>
                        <input disabled={true} value={currentUser.user.Email} className="form-control bg-gray" type="text" />
                      </span>
                    </li>
                    <li>
                      Mobile Number :{" "}
                      <span>
                        <input value={currentUser.user.mobileNumber} className="form-control bg-gray" type="text" />
                      </span>
                    </li>

                    {/* <li>
                          My Post :{" "}
                          <a href="/">
                            <span>View</span>
                          </a>
                        </li> */}
                  </ul>

              
                </div>
                <div className="col-lg-4 mt-40">
                    <button className="btn btn-primary upgrade-btn">
                      Upgrade
                    </button>
                  </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <Footer />
    </div>
  );
};

export default EDITSpROFILE;
